// R-7 per <http://en.wikipedia.org/wiki/Quantile>
export default function quantile(values, p) {
  let H = (values.length - 1) * p + 1,
      h = Math.floor(H),
      v = +values[h - 1],
      e = H - h;
  return e ? v + e * (values[h] - v) : v;
}



